import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  constructor() { }
  title = "Swine Tax"
  
  ngOnInit() {
  }

  public showListen: boolean = false;
  public showSocial: boolean = false;
  public showContact: boolean = false;

  public showListenTrigger(){
    this.showListen = true;
    this.showSocial = false;
    this.showContact = false;
  }
  public showSocialTrigger(){
    this.showListen = false;
    this.showSocial = true;
    this.showContact = false;
  }
  public showContactTrigger(){
    this.showListen = false;
    this.showSocial = false;
    this.showContact = true;
  }

}
